.home-info-container {
  display: flex;
  flex-flow: column wrap;
  margin: 20px;
  width: 75%;
}

.info-container {
  align-items: center;
  background-color: #D1D1C7;
  color: #1D2020;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  padding: 10px;

}

.info-container img {
  max-height: 225px;
  max-width: 300px;
  flex: 1 1 auto;
}

.info-container p {
  margin: 5px 0;
  padding: 15px 15px;
  max-width: 450px;
  flex: 2 1 auto;
  text-align: justify;
  text-indent: 3em;
  text-justify: inter-character;
}

.skill-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  margin-top: 30px;
}

.skill-container div {
  margin: 0 10px;
}

.skill-container ul {
  list-style-type: circle;
  padding-left: 20px;
}
