.footer-container {
  background-color: #3A4040;
  display: flex;
  flex-flow: column wrap;
  margin-top: 80px;
}

.footer-container h4 {
  margin: 20px 0 3px;
}

.footer-contact-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  margin: 5px 20px;
}

.footer-social-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  margin: 5px 20px 60px;
}

.footer-contact-container span {
  padding: 5px 30px;
}

.footer-social-container h4 {
  width: 100%;
}

.footer-social-container div {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 15px;
}

.footer-social-container a {
  background-color: #949E9E;
  border-radius: 5px;
  color: #1D2020;
  margin: 0 5px;
  padding: 5px 30px;
  text-decoration: none;
}
