.header-container {
  display: flex;
  flex-flow: column wrap;
}

.header-container h1 {
  text-align: center;
  margin-bottom: 5px;
}

.header-container h3 {
  text-align: center;
  margin: 5px;
}

.nav-container {
  display: flex;
  flex-flow: column wrap;
  font-size: 18px;
  margin: 25px;
  position: fixed;
}

.nav-container a {
  background-color: #949E9E;
  border-radius: 3px;
  color: #1D2020;
  margin: 5px 0;
  padding: 10px;
  text-align: center;
  text-decoration: none;
}
