.resume-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  margin-bottom: 100px;
}

.resume-container h3 {
  font-size: 32px;
  font-weight: 800;
  padding: 5px 60px;
  text-align: center;
}

.resume-section {
  margin-bottom: 10px;
  padding: 5px 60px;
  width: 70%;
}

.strong-paragraph {
  font-weight: 600;
}

.job-description {
  border: 1px solid black;
  padding: 5px 20px;
}
