.contact-container {
  display: flex;
  flex-flow: column wrap;
}

.contact-section-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
}

.contact-section-container span {
  text-align: center;
  width: 40%;
}

.contact-section-container img {
  border-radius: 12px;
  margin: 20px;
  max-width: 200px;
}

.contact-info-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  margin: 5px 20px;
}

.contact-info-container span {
  padding: 5px 30px;
}

.contact-social-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 5px 20px 60px;
}

.contact-social-container h4 {
  text-align: center;
  width: 100%;
}

.contact-social-container a {
  background-color: #949E9E;
  border-radius: 5px;
  color: #1D2020;
  margin: 0 5px;
  padding: 5px 30px;
  text-decoration: none;
}
