.aside-container {
  align-items: center;
  border: 1px solid #D1D1C7;
  display: flex;
  flex-flow: column wrap;
  margin: 20px 20px 0;
  min-width: 210px;
  width: 15%;
}

.aside-section-container {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  text-align: center;
}

.aside-section-container h4 {
  margin: 20px 0 3px;
}

.aside-section-container ul {
  list-style-type: square;
  margin: 5px 0 15px;
  padding: 3px 30px;
}

.aside-section-container a {
  color: #949E9E;
  padding: 3px;
  text-decoration: none;
}

.link-project {
  border: 1px solid #949E9E;
  border-radius: 3px;
  padding: 3px;
  text-align: center;
  text-decoration: none;
  width: 90%;
}
