h4 {
  text-align: center;
}

.project-main-page{
  align-items: center;
  display: flex;
  flex-flow: column wrap;

}

.projects-container {
  background-color: #303636;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 85%;
}

.project-item {
  display: flex;
  flex-flow: column wrap;
  padding: 20px 10px;
}

.project-item iframe {
  background-color: #f6f5f3;
  margin-bottom: 5px;
}

.project-item a {
  background-color: #949E9E;
  border-radius: 3px;
  color: #1D2020;
  margin: 5px 0;
  padding: 5px;
  text-align: center;
  text-decoration: none;
}
